div.item i {
    transform: translate(-50%, -50%) scale(0);
    transition: transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  }
  
  div.item:hover i {
    transform: translate(-50%, -50%) scale(1);
    transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }




  .img-cg-section {
    position: relative;
    width: 50%;
  }
  
  .cg-img {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .cg-img:hover {
    opacity: 0.8;
  }

  .middle-btn:hover {
    background-color: grey;
    color: white;
  }
  
  .img-cg-section .middle:hover {
    opacity: 1;
  }

  .cg-img:hover ~ .middle {
    opacity: 1;
  }
  
  .middle-btn {
    background-color: #4682b4;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
    font-weight: bold;
    font-size: 15px;
    border: 3px solid rgb(0, 0, 0);
  }

